.butn {
  padding: 12px 35px;
  background: #fff;
  border-radius: 30px;
  border: 1px solid transparent;
  position: relative;
  z-index: 3;
  margin-left: 10px;
  margin-right: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
  cursor: pointer;
  outline: 0 !important;
}
.butn span {
  position: relative;
  z-index: 2;
  border-radius: 30px;
}
.butn:after {
  border-radius: 30px;
  content: "";
  width: 0;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  transition: width 0.4s;
  z-index: 1;
  opacity: 1;
}
.butn:hover:after {
  border-radius: 30px;
  width: 100%;
  border-radius: 30px;
}
.butn-bg {
  background: #1a237e;
  border-color: #1a237e;
  color: #fff;
  border-radius: 30px;
}
.butn-bg:hover span {
  color: #1a237e;
  border-radius: 30px;
}
.butn-bord,
.butn-light {
  background: #fff;
  border-radius: 30px;
}
.butn-bord:after,
.butn-light:after {
  background: #1a237e;
  border-radius: 30px;
}
.butn-bord:hover span,
.butn-light:hover span {
  color: #fff !important;
  border-radius: 30px;
}
.butn-bord span,
.butn-light span {
  color: #1a237e;
  border-radius: 30px;
}
.butn-bord {
